.text-container {
  font-size: 18px;
}

.image-container {
  max-width: 80%;
  max-height: 80%;
  object-fit: fill;
}

.image-container--border {
  @extend .image-container;
  border-spacing: 1px;
  border-style: solid;
  border-width: 1px;
}

.image-container--multi {
  max-width: 30%;
  max-height: 30%;
  object-fit: fill;
}
